<template>
  <div class="page" id="member">
    <div class="content">
      <div class="p15 orange f14 tips" v-if="!hasSubmit">
        成为会员后，将会共享企业家之间的活动、专题分享、
        商务资源对接等资源共享。
      </div>
      <div class="p15 orange f14 tips" v-if="formData.status == 0">
        感谢您申请会员，我们将在3个工作日内为您审核。
      </div>
      <div class="p15 orange f14 tips" v-if="formData.status == 1">
        已通过会员申请，我们将会联系您并上门拜访。
      </div>
      <van-cell-group v-if="!hasSubmit">
        <van-field v-model="formData.name" placeholder="请输入申请人名称，必填"/>
        <van-field v-model="formData.tel" placeholder="请输入申请人电话，必填"/>
        <van-field v-model="formData.company" placeholder="请输入申请人企业名称，必填"/>
        <van-field v-model="formData.position" placeholder="请输入您的职位，必填"/>
      </van-cell-group>
      <van-cell-group v-else>
        <van-field disabled label="申请信息"
                   :class="{'revieving': formData.status == 0, 'reject': formData.status == 2, 'pass': formData.status == 1}"
                   input-align="right" :value="applyMsg[formData.status]"/>
        <van-field v-if="formData.status == 2" input-align="right" disabled label="驳回原因" :value="formData.reject"/>
        <van-field disabled label="姓名" input-align="right" :value="formData.name"/>
        <van-field disabled label="企业" input-align="right" :value="formData.company"/>
        <van-field disabled label="职位" input-align="right" :value="formData.position"/>
        <van-field disabled label="申请时间" input-align="right" :value="formData.time"/>
      </van-cell-group>
      <div class="btn-wrap mt10" v-if="!hasSubmit">
        <div class="btn btn-success btn-round" @click="applyMember()">提交申请</div>
      </div>
      <div class="btn-wrap mt10" v-if="hasSubmit && formData.status == 2">
        <div class="btn btn-success btn-round" @click="applyMember(1)">重新提交申请</div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'member',
    data() {
      return {
        hasSubmit: false,
        applyMsg: ['待审核', '已通过', '已驳回'],
        formData: {
          name: '',
          tel: '',
          company: '',
          position: ''
        }
      }
    },
    created() {
      this.getUserInfo()
      this.getMemberDetail()
    },
    methods: {
      getMemberDetail() {
        this.axios.get(this.apiConfig.joinMemberDetail).then(res => {
          if (res.s === 1) {
            let data = res.d
            if (data.name && data.tel && data.company && data.position) {
              this.hasSubmit = true
              this.formData = data
            } else {
              this.formData.tel = this.userInfo.phone
              this.formData.name = this.userInfo.username
              this.formData.company = this.userInfo.enterprise_name.length && this.userInfo.enterprise_name[0]
            }
            this.$store.dispatch('UPDATE_LOADINGSTATE', false)
          }
        })
      },
      getUserInfo() {
        this.axios.post(this.apiConfig.getUserInfo).then(res => {
          if (res.s === 1) {
            this.userInfo = res.d
          }
        })
      },
      applyMember(type) {
        let url
        let params
        this.formData.company_id = this.formData.coid
        this.formData.member_id = this.userInfo.user_id
        let {name, tel, company, position, company_id, member_id, id} = this.formData
        if (type == 1) {
          url = this.apiConfig.retryJoinMember
          params = {name, tel, company, position, company_id, member_id, id}
        } else {
          url = this.apiConfig.joinMember
          params = {name, tel, company, position}
        }
        if (!this.formData.name) {
          this.$toast('请输入申请人名称')
          return
        }
        if (!this.formData.tel) {
          this.$toast('请输入申请人电话')
          return
        } else if (!(/^1[34578]\d{9}$/.test(this.formData.tel))) {
          this.$toast("手机号码有误");
          return;
        }
        if (!this.formData.company) {
          this.$toast('请输入申请人公司')
          return
        }
        if (!this.formData.position) {
          this.$toast('请输入申请人职位')
          return
        }
        this.axios.post(url, params).then(res => {
          if (res.s === 1) {
            this.$toast('提交成功')
          }
        })
      }
    }
  }
</script>
<style lang="scss">
  #member {
    .tips {
      background-color: #fff4ea;
    }
    .reject .van-field__control {
      color: #f2821e;
    }
    .pass .van-field__control {
      color: #4bcf96;
    }
    .revieving .van-field__control {
      color: #519ed9;
    }
  }

</style>
